import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
} from '@material-ui/core';
import { Fabs } from '../compontents/Fabs';

const useStyles = makeStyles((theme) => ({
  width: {
    minWidth: '250px',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

export function CreateRecipeModal({ fab, value, index }) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  return (
    <div>
      <Fabs fab={fab} value={value} index={index} setOpen={setOpen} />
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={() => setOpen(false)}
      >
        <DialogTitle>Vytvořit Recept</DialogTitle>
        <DialogContent className={classes.width}>
          <TextField
            autoFocus
            margin='dense'
            id='name'
            label='Název Receptu'
            type='email'
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpen(false)} color='primary'>
            Cancel
          </Button>
          <Button onClick={() => setOpen(false)} color='primary'>
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
